				"title": "myThingsからみたIoTの可能性",
				"description": "2015年7月に「すべて」をつなげるというコンセプトでmyThingsというサービスをリリースしました。\n「すべて」の中にはWebや企業、人、街、データ、そして昨今すさまじい勢いで進化しているIoTデバイスも含みます。\n今回はそのmyThingsを設計・開発・運用していく中でみえてきたIoTの可能性についてお話できればと思います。\n",
				"profile": "# 略歴\nヤフー株式会社所属のエンジニア。ヤフーではYahoo!クラウドソーシングの基盤設計・開発、スクラムマスターを担当した後、IoT領域の新サービスであるmyThingsでもスクラムマスターとして開発に従事した。myThingsリリース後は、myThingsの魅力をより多くのエンドユーザー、開発者に伝えるべくmyThingsのエバンジェリストとして活動を行っている。好きな言葉は自動化、見える化、見せる化。\n",
