				"description": "Microsoft の Cognitive Services を利用すると、写真のデータを解析し人間の性別や年齢、感情などを推測することができます。本セッションでは Cognitive Services のアプリケーション開発から、IoT を利用したデモでご覧いただくほか、マイクロサービスを活用した開発手法の注意点についてご紹介します。",
				"profile": "2001 年 サン・マイクロシステムズ株式会社に入社し GlassFish エバンジェリストとして活動。その後、日本オラクル株式会社で Java エバンジェリストとして活動。\n2015 年 7月、日本マイクロソフト株式会社に移籍し、移籍後もなお Java エバンジェリストとして、マイクロソフト・プラットフォームにおける Java の利用促進・啓蒙活動を実施中。2016 年 7 月、日本人で 2 人目となる Java Champion に就任。\n",
