				"title": "ワクワクエンジニアライフの提案",
				"description": "広島と言えばモノづくり。IoTやシェリングエコノミーの出現により、行く末が不透明になってきています。コトづくりへのシフトに備える必要性を、クルマを事例に紹介します。ICTエンジニアがそのシフトに対する救世主となり、ワクワクする広島であり続けるため、そして自身もワクワクするエンジニアライフを過ごすために、広島のICTコミュニティのあるべき姿を提案します。",
				"image": "/img/2017_motoki@2x.jpg",
				"profile": "2015年8月、ハッカソンという単語も知らずにハッカソンに参加し、ICTをツールとしたオープンイノベーションに目覚める。\n以来、アイデアソン／ハッカソンのみならず各種イベントに参加することでICTコミュニティを作りながら、広島の存在感増幅を目指して活動中。\nまた、17年間楽しんでいるサイクリングについても、ICTのチカラを借りてイノベーションを起こす事を企み中。\n",
