				"title": "子どもプログラミングとドリームマップ",
				"description": "\"\n子どもたちの個性を伸ばし、夢を叶えることを応援する活動の中で子どもプログラミング教室を開講して１年が経ちました。\nITが一般的になった社会では、プログラミングができるかどうかで大きな差がついてしまいます。\nプログラマの皆さんは、プログラミングを通して、論理的思考力と物事の仕組みへの探求心を育まれてこられたことと思います。\nこれらの能力によって子どもたちが夢に大きく近づくことができると思います。\n今回は、子どもプログラミング教室を開校して見えてきたことと、そこから得られたことから、現役プログラマさんにお伝えしたいことをお話したいと思います。\n",
				"profile": "電機メーカー勤務を経てパソコンインストラクターになり、県内私立大学や専門学校の非常勤講師、企業研修、その他官・民主催のパソコン講習の講師となる。\n2016年3月より子どもプログラミング教室　Dream Tech Schoolを開校\n現在　牛田、古田台、光明学園、舟入、宇品、広島YMCA学園の６教室で展開中。\nまた、光明学園、YMCA国際幼児園、なぎさ公園小学校にプログラミング授業を企画・提案し、実施することにより、子どもたちのより良い未来のために奮闘するかたわら、一般社団法人ドリームマップ普及協会の認定ドリマ講師として、小・中・高等学校等でドリームマップ授業を実施中。\n",
